/*
* ==========================================================
*   Cards
* ==========================================================
*/
.card {
  border: none;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}

.card-header {
  position: relative;
  padding: 2rem 2rem;
  border-bottom: none;
  background-color: white;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
  z-index: 2;
}

.card-header:first-child {
  border-radius: calc(1rem - 1px) calc(1rem - 1px) 0 0;
}

.card-header-tabs {
  margin-bottom: -2rem;
}

.card-header-tabs .nav-link {
  margin-bottom: 0;
  border-bottom: none;
}

.card-header-transparent {
  border-bottom: none;
  background-color: rgba(0, 0, 0, 0.3);
}

.card .list-group-item {
  padding-right: 2rem;
  padding-left: 2rem;
}

.card-heading {
  margin-bottom: 0;
  font-size: 0.9rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.2em;
}

.credit-card {
  position: relative;
  height: 0;
  padding-top: 66%;
  border-radius: 0.5rem;
}

.credict-card-content {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 1.5rem;
  justify-content: space-between;
}

.credict-card-bottom {
  display: flex;
  padding-top: 1rem;
  justify-content: space-between;
  align-items: flex-end;
  flex-wrap: wrap;
}

.card-profile .card-header {
  height: 9rem;
  background-position: center center;
  background-size: cover;
}

.card-profile-img {
  position: relative;
  max-width: 8rem;
  margin-top: -6rem;
  margin-bottom: 1rem;
  border: 3px solid #fff;
  border-radius: 100%;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  z-index: 2;
}

.card-status {
  position: absolute;
  top: -1px;
  right: -1px;
  left: -1px;
  height: 8px;
  background: rgba(0, 40, 100, 0.12);
  border-radius: calc(1rem - 1px) calc(1rem - 1px) 0 0;
}

.card-img-overlay-top {
  position: absolute;
  top: 2rem;
  right: 2rem;
  left: 2rem;
}

.card-img-overlay-bottom {
  position: absolute;
  right: 2rem;
  bottom: 2rem;
  left: 2rem;
}

.card-widget {
  background: #fff;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 50rem;
  padding: 1.5rem;
}

.card-widget-body {
  display: flex;
  align-items: center;
  flex-grow: 1;
}

.card-category {
  text-transform: uppercase;
  color: #6c757d;
  font-size: 0.9rem;
  font-weight: bold;
}

