.sidebar-menu {
  position: relative;
  background: #f8f9fa;
  box-shadow: inset 4px -2px 0.25rem 0px rgba(0, 0, 0, 0.075);
  font-size: 0.7875rem;
}

.sidebar-heading {
  margin-bottom: 0;
  padding: 1.5rem;
  color: #adb5bd;
  font-size: 0.7875rem;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.2em;
}

@media (max-width: 991.98px) {
  .sidebar-heading {
    padding-right: 0.5rem;
    padding-left: 0.5rem;
    text-align: center;
  }
}

.sidebar-link {
  display: flex;
  padding: 0.5rem 1.5rem;
  transition: background 0.3s;
  text-decoration: none;
  align-items: center;
}

.sidebar-link[data-bs-toggle="collapse"] {
  position: relative;
}

.sidebar-link[data-bs-toggle="collapse"]::before {
  display: block;
  position: absolute;
  top: 50%;
  right: 1rem;
  content: '\f104';
  transform: translateY(-50%);
  color: #aaa;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
}

.sidebar-link[data-bs-toggle="collapse"][aria-expanded='true']::before {
  content: '\f107';
}

.sidebar-link:hover {
  text-decoration: none;
  background: rgba(0, 0, 0, 0.03);
}

.sidebar-link.active, .sidebar-link:focus {
  text-decoration: none;
  color: #fff !important;
  background: #9a6ee2;
}

.sidebar-link.active::before, .sidebar-link:focus::before {
  color: #fff;
}

.sidebar-link:focus {
  background: #a47de5;
}

.sidebar-link .sidebar-link-title {
  display: inline-block;
  transition: all 0.3s;
}

.sidebar-menu .sidebar-link.active {
  background: #b89aeb;
}

.sidebar {
  width: 16rem;
  transition: all 0.2s;
  background: #fff;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  z-index: 2;
}

.sidebar.shrink {
  width: 6rem;
  text-align: center;
}

.sidebar.shrink .sidebar-link {
  display: block;
  padding-right: 0.2rem;
  padding-left: 0.2rem;
  font-size: 0.75rem;
}

.sidebar.shrink .sidebar-link .sidebar-link-title {
  display: block;
  font-size: 0.75rem;
}

@media (max-width: 575.98px) {
  .sidebar.shrink .sidebar-link .sidebar-link-title {
    font-size: 0.6rem;
  }
}

.sidebar.shrink .sidebar-link svg {
  margin: 0 !important;
}

.sidebar.shrink .sidebar-link[data-bs-toggle="collapse"] {
  padding-bottom: 1.5rem;
}

.sidebar.shrink .sidebar-link[data-bs-toggle="collapse"]::before {
  position: absolute;
  top: auto;
  right: 50%;
  bottom: .25rem;
  left: auto;
  content: '\f107';
  transform: translateX(50%);
}

.sidebar.shrink .sidebar-link[data-bs-toggle="collapse"][aria-expanded='true']::before {
  content: '\f106';
}

.sidebar.shrink .sidebar-heading {
  padding-right: 0.5rem;
  padding-left: 0.5rem;
  text-align: center;
}

.sidebar.shrink .sidebar-menu {
  font-size: 0.7875rem;
}

.sidebar.shrink .collapse .sidebar-link,
.sidebar.shrink .collapsing .sidebar-link {
  padding-right: 0.2rem !important;
  padding-left: 0.2rem !important;
  transition: none;
}

@media (max-width: 1199.98px) {
  .sidebar {
    overflow: hidden;
    width: 6rem;
    margin-left: -6rem;
    transition: all 0.4s;
    text-align: center;
  }
  .sidebar .sidebar-link {
    display: block;
    padding-right: 0.2rem;
    padding-left: 0.2rem;
  }
  .sidebar .sidebar-link .sidebar-link-title {
    display: block;
    font-size: 0.75rem;
  }
  .sidebar .sidebar-link svg {
    margin: 0 !important;
  }
  .sidebar .sidebar-heading {
    padding-right: 0.5rem;
    padding-left: 0.5rem;
    text-align: center;
  }
  .sidebar .sidebar-menu {
    font-size: 0.7875rem;
  }
}

.sidebar.show {
  margin-left: 0;
}

