/*
     Transparent overlays
    ------------------------
*/
.light-overlay,
.dark-overlay,
.gradient-overlay {
  position: relative;
  overflow: hidden;
}

.light-overlay .overlay-content,
.dark-overlay .overlay-content,
.gradient-overlay .overlay-content {
  position: relative;
  z-index: 20;
}

.light-overlay::after,
.dark-overlay::after,
.gradient-overlay::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: ' ';
  opacity: .3;
  background: #000;
}

.light-overlay::after {
  opacity: .3;
  background: #fff;
}

.gradient-overlay::after {
  opacity: 1;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 78%, rgba(0, 0, 0, 0.65) 100%);
}

