}

.mh-full-screen {
  min-height: 100vh;
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  .mh-full-screen {
    height: 100vh;
  }
}

.list-group {
  border-radius: 0.25rem;
}

@media print {
  .shadow-print-0 {
    box-shadow: none !important;
  }
  .px-print-0 {
    padding-right: 0 !important;
    padding-left: 0 !important;
  }
  .p-print-0 {
    padding: 0 !important;
  }
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  .card {
    display: block;
  }
  .card-img-top,
  .card-img-bottom,
  .card-img {
    width: auto;
    min-width: 100%;
    height: auto;
  }
}

